export const createMatchFunction = (method) => {
  return (...functions) => {
    return (...objects) => {
      return functions[method]((fn) => {
        return !!fn(...objects);
      });
    };
  };
};

export const matchAll = createMatchFunction('every');
export const matchAny = createMatchFunction('some');

export default {
  all: matchAll,
  any: matchAny
};



